// Components
import React, { Component } from 'react';
import { graphql } from 'gatsby';

import 'gitalk/dist/gitalk.css';

import { parseDate } from '../api';
import Sidebar from '../components/Sidebar';
import Content from '../components/Content';
import SEO from '../components/SEO';

import Header from '../components/Header';
// import TableOfContent from '../components/TableOfContent';
import ShareBox from '../components/ShareBox';

import config from '../../data/config.json';
// Styles
import './blog-post.scss';

const { name, iconUrl, gitalk } = config;

// Prevent webpack window problem
const isBrowser = typeof window !== 'undefined';
const Gitalk = isBrowser ? require('gitalk') : undefined;

class BlogPost extends Component<any> {
    private data: any;

    constructor(props: Readonly<any>) {
        super(props);
        this.data = this.props.data;
    }

    componentDidMount() {
        const { frontmatter, id: graphqlId } = this.data.content.edges[0].node;
        const { title, id } = frontmatter;

        const GitTalkInstance = new Gitalk({
            ...gitalk,
            title,
            id: id || graphqlId,
        });
        GitTalkInstance.render('gitalk-container');
    }

    render() {
        const { node } = this.data.content.edges[0];

        const {
            html, frontmatter, fields, excerpt,
        } = node;

        const { slug } = fields;

        const { date, headerImage, title } = frontmatter;

        return (
            <div className="row post order-2">
                <Header
                    img={headerImage || 'https://picsum.photos/1900/450'}
                    title={title}
                    authorName={name}
                    authorImage={iconUrl}
                    subTitle={parseDate(date)}
                />
                <Sidebar />
                <div className="col-xl-7 col-lg-6 col-md-12 col-sm-12 order-10 content">
                    <Content post={html} />
                    <div id="gitalk-container" />
                </div>

                <ShareBox url={slug} />

                <SEO
                    title={title}
                    url={slug}
                    siteTitleAlt="Calpa's Blog"
                    isPost={false}
                    description={excerpt}
                    image={headerImage || 'https://i.imgur.com/M795H8A.jpg'}
                />
            </div>
        );
    }
}

export const pageQuery = graphql`
  fragment post on MarkdownRemark {
    fields {
      slug
    }
    frontmatter {
      id
      title
      slug
      date
      headerImage
    }
  }

  query BlogPostQuery($index: Int) {
    content: allMarkdownRemark(
      sort: { order: DESC, fields: frontmatter___date }
      skip: $index
      limit: 1
    ) {
      edges {
        node {
          id
          html
          excerpt
          ...post
        }

        previous {
          ...post
        }

        next {
          ...post
        }
      }
    }
  }
`;

export default BlogPost;
